import fetch from '@/utils/fetch1';
const qs = require('qs');

export function add(params) {
    return fetch({
        url: '/api/banner/add',
        method: 'post',
        data: qs.stringify(params),
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
}

export function lists(params) {
    return fetch({
        url: '/api/banner/lists',
        method: 'post',
        data: qs.stringify(params),
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
}

export function dlBanner(params) {
    return fetch({
        url: '/api/banner/delete',
        method: 'post',
        data: qs.stringify(params),
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    })
}

export function edit(params) {
    return fetch({
        url: '/api/banner/edit',
        method: 'post',
        data: qs.stringify(params),
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        }
    });
}



