<template>
  <div class="app-container carousel">
    <div class="header">
        <div class="title">首页轮播图管理</div>
        <div class="btn-op">
            <div v-if="!isEdit">
                <el-button type="primary" @click="editBanner">编辑</el-button>
            </div>
            <div v-if="isEdit">
                <el-button v-if="!sortable && !isDel" type="primary" icon="plus" :disabled="!isCanAddItem" @click="addItem">添加</el-button>
                <el-button v-if="!sortable && !isDel" type="primary" @click="canSortable">排序</el-button>
                <el-button v-if="sortable || isDel" type="primary" @click="topSubmit">确定</el-button>
                <el-button v-if="!sortable && !isDel" type="danger" @click="canDel">删除</el-button>
            </div>
        </div>
    </div>
    <div class="msg">建议上传的图片尺寸为 900x450 ，且不超过5MB！</div>
    <div class="carousel-wrap">
      <input ref="bannerImg" type="file" accept="image/jpeg,image/jpg,image/png" style="opacity: 0; position: fixed; top: -1000px;" @change="uploadImg">      
      <draggable v-model="list" :options="dragOptions" @end="onEnd">
        <div class="carousel-item" v-for="(item, index) in list" :key="index">
            <div class="top" :style="{ cursor: sortable ? 'move' : 'default' }">
                <div class="order">图片{{ item.sort + 1 }}</div>
                <div v-if="isDel" class="delete" @click="delItem(index)"><i class="el-icon-delete2"></i></div>
            </div>
            <div class="content">
                <el-form ref="form" :model="item" label-width="80px">
                    <el-form-item label="图片" required>
                        <div :style="{ cursor: !isUpload ? 'pointer' : 'wait'}" class="upload-wrap" @click="emitFile(index)">
                            <i v-if="!item.pic" class="el-icon-plus"></i>
                            <img v-if="item.pic" :src="'http://' + item.pic">
                        </div>
                    </el-form-item>
                    <el-form-item label="链接">
                        <el-input v-model="item.url" :disabled="!isEdit"></el-input>
                    </el-form-item>
                </el-form>
            </div>
        </div>
      </draggable>
    </div>
    <div class="submit" v-if="isEdit">
        <el-button v-if="!sortable && !isDel" type="primary" @click="submitAll">提交</el-button>
        <el-button v-if="!sortable && !isDel" @click="cancel">取消</el-button>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import { add, lists, dlBanner, edit } from "@/api/carousel";
import { upload } from "@/api/member";
import { imgBeforeUpload } from "@/utils";

export default {
  components: {
    draggable
  },
  data() {
    return {
      list: [],
      sortable: false,
      isDel: false,
      // 删除的banner
      dlList: [],
      activeIndex: 0,
      isEdit: false,
      isUpload: false
    };
  },
  computed: {
    // 还能否添加banner
    isCanAddItem() {
      return this.list.length < 5;
    },
    dragOptions() {
      return {
        handle: ".top",
        ghostClass: "ghost",
        draggable: ".carousel-item",
        disabled: !this.sortable
      };
    }
  },
  created() {
    this.getBannerList();
  },
  methods: {
    onEnd(evt) {
      for (let i = 0, length = this.list.length; i < length; i++) {
        this.list[i].sort = i;
      }
      console.log(evt, this.list);
    },
    // 获取banner列表
    getBannerList() {
      lists().then(response => {
        this.list = response.data.list.reverse();
      });
    },
    // 添加一个banner
    addItem() {
      let length = this.list.length;
      if (length < 5) {
        this.list.push({
          pic_id: "",
          sort: length,
          url: "",
          pic: ""
        });
      }
    },
    // 上传图片
    uploadImg(e) {
      let file = e.target.files[0];
      if (!file) {
        return;
      }
      this.isUpload = true;
      // imgBeforeUpload 检测图片类型和压缩图片
      imgBeforeUpload(file)
        .then(rst => {
          upload(rst.formData).then(response => {
            let index = this.activeIndex;
            this.list[index].pic = response.data.pic_address;
            this.list[index].pic_id = response.data.pic_id;
            this.isUpload = false;
          });
        })
        .catch(err => {
          this.$message.error(err.toString());
          this.isUpload = false;
        });
    },
    // 触发input
    emitFile(index) {
      if (this.isUpload || !this.isEdit) {
        return;
      }
      this.activeIndex = index;
      this.$refs.bannerImg.click();
    },
    // 排序
    canSortable() {
      this.sortable = true;
    },
    // 顶部的确定按钮
    topSubmit() {
      if (this.sortable) {
        console.log("排序");
      } else {
        console.log("删除");
      }
      this.sortable = false;
      this.isDel = false;
    },
    // 删除
    canDel() {
      this.isDel = true;
    },
    // 提交按钮
    submitAll() {
      if (!this.checkCanSubmit()) {
        this.$message.error("图片不能为空！");
        return;
      }

      // 状态
      let state = [],
        delState = [];
      let listLength = this.list.length;
      let delLength = this.dlList.length;
      for (let i = 0; i < listLength; i++) {
        state[i] = false;
      }
      for (let i = 0; i < delLength; i++) {
        delState[i] = false;
      }

      console.log(this.list, this.dlList);

      for (let i = 0; i < listLength; i++) {
        // 如果有id就是更新
        if (this.list[i].id) {
          debugger;
          edit(this.list[i]).then(response => {
            state[i] = true;
            if (this.checkFinish(state) && this.checkFinish(delState)) {
              console.log("state第 " + i + " 个");
              console.log("完成");
              this.$message.success("提交成功！");
              this.getBannerList();
              this.isEdit = false;
            } else {
              console.log("state第 " + i + " 个");
            }
          });
        } else {
          // 没有id就是新建
          debugger;
          add(this.list[i]).then(response => {
            state[i] = true;
            if (this.checkFinish(state) && this.checkFinish(delState)) {
              console.log("state第 " + i + " 个");
              console.log("完成");
              this.$message.success("提交成功！");
              this.getBannerList();
              this.isEdit = false;
            } else {
              console.log("state第 " + i + " 个");
            }
          });
        }
      }
      for (let i = 0; i < delLength; i++) {
        dlBanner({ id: this.dlList[i] }).then(response => {
          delState[i] = true;
          if (this.checkFinish(state) && this.checkFinish(delState)) {
            console.log("delState第 " + i + " 个");
            console.log("完成");
            this.$message.success("提交成功！");
            this.getBannerList();
            this.isEdit = false;
          } else {
            console.log("delState第 " + i + " 个");
          }
        });
      }
    },
    // 判断是否已经完成
    checkFinish(list) {
      for (let j = 0; j < list.length; j++) {
        if (!list[j]) {
          return false;
        }
      }
      return true;
    },
    // 删除
    delItem(index) {
      if (this.list[index].id) {
        console.log("有id");
        this.dlList.push(this.list[index].id);
      }
      this.list.splice(index, 1);
      console.log(this.dlList);
      for (let i = 0, length = this.list.length; i < length; i++) {
        this.list[i].sort = i;
      }
    },
    editBanner() {
      this.isEdit = true;
    },
    cancel() {
      this.getBannerList();
      this.isEdit = false;
    },
    // 判断市某可以提交
    checkCanSubmit() {
      for (let i = 0; i < this.list.length; i++) {
        if (!this.list[i].pic_id) {
          return false;
        }
      }
      return true;
    }
  }
};
</script>

<style lang="scss">
.carousel {
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
    padding: 20px;
    border: 1px solid #e3e8f0;
    margin-bottom: 10px;
    .title {
      font-size: 20px;
    }
  }
  .msg {
    font-size: 12px;
    color: #7c7c7c;
    line-height: 2;
    color: #ff4949;
    &:before {
      content: "*";
      margin-right: 4px;
    }
  }
  .carousel-wrap {
    .carousel-item {
      border: 1px solid #e3e8f0;
      background: #fff;
      margin-bottom: 10px;
      &.ghost {
        opacity: 0.5;
        background: #c8ebfb;
      }
      .top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        background: #e4e6ea;
        .delete {
          cursor: pointer;
          &:hover {
            color: #ff4949;
          }
        }
      }
      .content {
        padding: 15px 0;
        .el-form {
          width: 50%;
          margin: auto;
          .upload-wrap {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 240px;
            height: 120px;
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            margin: 10px;
            &:hover {
              border-color: #20a0ff;
            }
            .el-icon-plus {
              font-size: 28px;
              color: #8c939d;
            }
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
  }
  .submit {
    margin-top: 20px;
    text-align: center;
  }
}
</style>



